import {mapState,mapActions} from 'vuex';
  export default {
    data() {
      return {
        mountains: [],
        hello:'HelloWorld'
      }
    },
    computed:{
      ...mapState({
        counter:state =>state.counter,
        mountainsState: state =>state.mountains
      })
    },
    methods:{
      ...mapActions([
        'increment',
        'changeMoutains'
      ]),
      AddEvent(){
        this.increment();
      }
      
    },
    async fetch() {
      var vm = this;

      this.mountains = await fetch(
        'https://api.nuxtjs.dev/mountains'
      ).then(res => res.json())
    
     this.$store.state.mountains = this.mountains;
     
    }
    
  }
